import { inject, Injectable } from '@angular/core';
import { DataLayerAttributes, DataLayerService, EventNameEnum, EventTypeEnum } from '@telenet/ng-lib-datalayer';
import * as udl from 'udl';

@Injectable({
  providedIn: 'root',
})
export class AppointmentDatalayerService {
  private readonly dataLayerService = inject(DataLayerService);

  readonly INTENT_RESCHEDULE_APPOINTMENT = 'reschedule appointment';
  readonly INTENT_NEW_APPOINTMENT = 'new appointment';
  readonly MESSAGE_GROUP = ['appointments'];
  readonly FLOWTYPE_CARE = 'care';

  currentIntent: string;
  componentInstanceId: string;

  sendRescheduleStartEvent(): void {
    const eventInfo = this.getEventInfo(
      EventNameEnum.EVENT_NAME_RESCHEDULE_APPOINTMENT_FLOW_STARTED,
      EventTypeEnum.EVENT_TYPE_SELF_SERVICE_FLOW_STARTED
    );
    this.currentIntent = this.INTENT_RESCHEDULE_APPOINTMENT;
    const attributes = this.getAttributes();
    this.addEvent(eventInfo, attributes);
  }

  sendRescheduleEndEvent(): void {
    const eventInfo = this.getEventInfo(
      EventNameEnum.EVENT_NAME_RESCHEDULE_APPOINTMENT_FLOW_COMPLETED,
      EventTypeEnum.EVENT_TYPE_SELF_SERVICE_FLOW_END
    );
    const attributes = this.getAttributes();
    this.addEvent(eventInfo, attributes);
    this.resetIntent();
  }

  sendCancelAppointmentEvent(): void {
    const eventInfo = this.getEventInfo(EventNameEnum.EVENT_NAME_CANCEL_APPOINTMENT, EventTypeEnum.EVENT_TYPE_CLICK);
    const attributes = {} as DataLayerAttributes;
    this.addEvent(eventInfo, attributes);
  }

  sendUpdateContactMethodEvent(itemName: string, itemData: string): void {
    const eventInfo = this.getEventInfo(
      EventNameEnum.EVENT_NAME_APPOINTMENT_OPTION_SELECTED,
      EventTypeEnum.EVENT_TYPE_CLICK
    );
    const attributes = this.getAttributes(itemName, itemData);
    this.addEvent(eventInfo, attributes);
  }

  sendSelectedDateTimeEvent(itemName: string): void {
    const eventInfo = this.getEventInfo(
      EventNameEnum.EVENT_NAME_APPOINTMENT_OPTION_SELECTED,
      EventTypeEnum.EVENT_TYPE_CLICK
    );
    const attributes = this.getAttributes(itemName);
    this.addEvent(eventInfo, attributes);
  }

  private getEventInfo(eventName: EventNameEnum, eventType: EventTypeEnum): udl.EventInfo {
    return this.dataLayerService.createEventInfo(eventName, eventType, this.MESSAGE_GROUP);
  }

  private getAttributes(itemName = '', itemData = ''): DataLayerAttributes {
    return {
      itemName,
      itemData,
      intent: this.currentIntent,
      flowType: this.FLOWTYPE_CARE,
    } as DataLayerAttributes;
  }

  private addEvent(eventInfo: udl.EventInfo, attributes: DataLayerAttributes): void {
    this.dataLayerService.addEvent(eventInfo, attributes, this.category);
  }

  private get category(): udl.Category {
    const category = this.dataLayerService.getCategory(this.componentInstanceId);
    return category || this.dataLayerService.createCategory('care', 'product journey');
  }

  resetIntent(): void {
    this.currentIntent = this.INTENT_NEW_APPOINTMENT;
  }
}
